'use strict';
require('es6-promise').polyfill();
import 'nodelist-foreach-polyfill';
import'dom-node-polyfills';
import 'whatwg-fetch';
import 'formdata-polyfill';

import tabs from './modules/tabs';
import modal from './modules/modal';
import timer from './modules/timer';
import cards from './modules/cards';
import calc from './modules/calc';
import forms from './modules/forms';
import slider from './modules/slider';
import {
    openModal
} from './modules/modal';

/*     DOM */
document.addEventListener("DOMContentLoaded", () => {

    /* 
         modalTimerId  modal.js
        1)   modalTimerId
        2)      
        3)            openModal
        4)  openModal     2 
        5) openModal('.modal', modalTimerId):
            1)   
            2)      
        6)              () => openModal('.modal', modalTimerId)
        7)  modalTimerId(  )   modal
        8)    openModal     main.js

        9)       forms.js    openModal,   modalTimerId
    */
    const modalTimerId = setTimeout(() => openModal('.modal', modalTimerId), 10000);

    tabs('.tabheader__item', '.tabcontent', '.tabheader__items', 'tabheader__item_active');
    modal('[data-modal]', '.modal', modalTimerId);
    timer('.timer', '2023-03-17');
    cards();
    calc();
    forms('form', modalTimerId);
    slider({
        container: '.offer__slider',
        slide: '.offer__slide',
        nextArrow: '.offer__slider-next',
        prevArrow: '.offer__slider-prev',
        totalCounter: '#total',
        currentCounter: '#current',
        wrapper: '.offer__slider-wrapper',
        field: '.offer__slider-inner',
    });
});